  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.summary-card {
  background-color: white;
  border-radius: var(--border-radius);
  padding: 1.5rem;
  text-align: center;
  box-shadow: var(--box-shadow);
}

.summary-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.summary-label {
  color: var(--text-secondary);
}

.adopters-list {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  overflow: hidden;
}

.badge-icon {
  display: inline-flex;
  align-items: center;
}

.badge-icon.pioneer {
  color: #f39c12;
}

.badge-icon.innovator {
  color: #9b59b6;
}

.badge-icon.contributor {
  color: #2ecc71;
}

.badge-icon.visionary {
  color: #3498db;
}

.no-badge {
  color: var(--text-secondary);
  font-style: italic;
}

.interests-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}

.interest-tag {
  padding: 0.25rem 0.5rem;
  border-radius: 999px;
  font-size: 0.75rem;
}

.interest-tag.identity {
  background-color: rgba(52, 152, 219, 0.1);
  color: #3498db;
}

.interest-tag.transparency {
  background-color: rgba(46, 204, 113, 0.1);
  color: var(--success-color);
}

.interest-tag.industrial {
  background-color: rgba(230, 126, 34, 0.1);
  color: #e67e22;
}

.action-button.view {
  background-color: var(--gray-200);
  color: var(--text-color);
}

.action-button.award {
  background-color: #f39c12;
  color: white;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  width: 100%;
  max-width: 600px;
  background-color: white;
  border-radius: var(--border-radius);
  overflow: hidden;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid var(--gray-200);
}

.modal-header h3 {
  margin-bottom: 0;
}

.close-modal {
  font-size: 1.5rem;
  color: var(--text-secondary);
  background: none;
  border: none;
  cursor: pointer;
}

.modal-body {
  padding: 1.5rem;
}

.badges-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.badge-card {
  background-color: var(--gray-100);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  cursor: pointer;
  transition: var(--transition);
  border: 2px solid transparent;
}

.badge-card:hover {
  background-color: var(--gray-200);
}

.badge-card.selected {
  border-color: var(--primary-color);
  background-color: rgba(10, 189, 227, 0.05);
}

.badge-card .badge-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.badge-card h4 {
  margin-bottom: 0.5rem;
}

.badge-card p {
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  padding: 1.25rem 1.5rem;
  border-top: 1px solid var(--gray-200);
}

.cancel-button {
  padding: 0.75rem 1.5rem;
  background-color: var(--gray-200);
  color: var(--text-color);
  border-radius: var(--border-radius);
  margin-right: 1rem;
}

.confirm-button {
  padding: 0.75rem 1.5rem;
  background-color: var(--primary-color);
  color: white;
  border-radius: var(--border-radius);
}

.confirm-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Login page */
.login-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, var(--secondary-color), var(--dark-color));
}

.login-container {
  width: 100%;
  max-width: 400px;
  background-color: white;
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.login-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  background-color: var(--gray-100);
  border-bottom: 1px solid var(--gray-200);
}

.login-logo img {
  height: 50px;
  margin-bottom: 1rem;
}

.login-logo h1 {
  font-size: 1.5rem;
  margin-bottom: 0;
}

.login-form-container {
  padding: 2rem;
}

.login-form-container h2 {
  margin-bottom: 0.5rem;
}

.login-form-container p {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.login-form {
  margin-bottom: 1.5rem;
}

.login-button {
  width: 100%;
  padding: 0.75rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: var(--border-radius);
  font-weight: 500;
  margin-top: 1rem;
}

.login-button:hover {
  background-color: var(--primary-dark);
}

.login-footer {
  display: flex;
  justify-content: space-between;
  font-size: 0.875rem;
}

/* Responsive styles */
@media (max-width: 992px) {
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
  
  .connectors-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }
  
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .section {
    padding: 3rem 1.5rem;
  }
  
  .admin-sidebar {
    width: 70px;
    overflow: hidden;
  }
  
  .admin-logo h1,
  .admin-nav a span,
  .view-site-link span {
    display: none;
  }
  
  .admin-main {
    margin-left: 70px;
  }
  
  .admin-nav a {
    justify-content: center;
    padding: 1rem;
  }
  
  .admin-nav a i {
    margin-right: 0;
    font-size: 1.35rem;
  }
  
  .admin-search {
    width: 200px;
  }
  
  .segment-buttons {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 576px) {
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.75rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .hero {
    padding: 4rem 1.5rem;
  }
  
  .hero-buttons {
    flex-direction: column;
  }
  
  .page-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .filter-bar {
    flex-direction: column;
    gap: 1rem;
  }
  
  .search-input {
    max-width: 100%;
  }
  
  .admin-header {
    flex-direction: column;
    gap: 1rem;
  }
  
  .admin-search {
    width: 100%;
  }
  
  .badges-grid {
    grid-template-columns: 1fr;
  }
  
  .hero h1 {
    font-size: 2rem;
  }
  
  .hero p {
    font-size: 1rem;
  }
  
  .grid-2, 
  .grid-3, 
  .grid-4 {
    grid-template-columns: 1fr;
  }
  
  .btn {
    width: 100%;
  }
}
